export default {
	data() {
		return {
			denglu: {
				phone: '',
				password: '',
				mescode: '',
				pwtype: '../../static/login/eye_close.png',
				imgtype: true,
				datastate: '1',
			}
		}
	},
	created() {
		var that = this;
		uni.getStorage({
			key: 'userinfo',
			success(res) {
				console.log(res);
				that.denglu.phone=res.data.mobile;
				that.denglu.password=res.data.password;
				that.login_tap();
				uni.request({
					url: that.serveipd + "/api/merchant/auth/approval",
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded',
					},
					data: {
						customerId: res.data.userid,
					},
					success(e) {
						if (e.data.data.status == 1) {
							uni.navigateTo({
								url: '/pages/Merchan_registration/in_audit',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						};
						if (e.data.data.status == 2) {
							uni.switchTab({
								url: '/pages/new_order/new_order',
							})
						};
						if (e.data.data.status == 3) {
							uni.navigateTo({
								url: '/pages/Merchan_registration/not_pass?id=' + res.data.userid,
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						};
						if (e.data.data.status == 4) {
							uni.navigateTo({
								url: '/pages/Merchan_registration/Merchan_registration?id=' + res.data.data.customer.id,
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						};
					}
				})
			}
		})
	},
	methods: {
		forgetfuc: function() {
			uni.navigateTo({
				url: './retrieve',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		pwshow: function() {
			var that = this;
			if (that.denglu.datastate == '1') {
				that.denglu.pwtype = '../../static/login/eye_open.png';
				that.denglu.datastate = '2';
				that.denglu.imgtype = false;
			} else {
				that.denglu.pwtype = '../../static/login/eye_close.png';
				that.denglu.datastate = '1';
				that.denglu.imgtype = true;
			}
		},
		login_tap: function() {
			var that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/auth/login',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				data: {
					mobile: that.denglu.phone,
					password: that.denglu.password
				},
				success: (res) => {
				res.data.data.name;
					uni.showToast({
						title: res.data.msg,
						icon: 'none',
						mask: false,
						duration: 1500
					});
					if (res.data.code == 0) {
						if (res.data.data.status == 2) {
							uni.switchTab({
								url: '/pages/new_order/new_order',
							})
						};
						if (res.data.data.status == 4) {
							uni.navigateTo({
								url: '/pages/Merchan_registration/Merchan_registration?id=' + res.data.data.customer.id,
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						};
						if (res.data.data.status == 1) {
							uni.navigateTo({
								url: '/pages/Merchan_registration/in_audit',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						};
						if (res.data.data.status == 3) {
							uni.navigateTo({
								url: '/pages/Merchan_registration/not_pass' + res.data.data.customer.id,
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						};
						uni.setStorage({
							key: 'userinfo',
							data: {
								mobile: that.denglu.phone,
								password: that.denglu.password,
								id: res.data.data.id,
								userid: res.data.data.customer.id,
								status: res.data.data.status,
								name:res.data.data.name,
							}
						});
					};

				},
				fail: (err) => {
					uni.showToast({
						title: '登录失败',
						mask: false,
						icon: 'none',
						duration: 1500
					});
				},
				complete: () => {}
			});
		},
		reg_page: function() {
			uni.navigateTo({
				url: '/pages/login/register',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
